import { Value, Document, Block } from '@ali/4ever-cangjie';
import Mo from '../serializer';

describe('Mo.valueToJsonML exception', () => {
  it('碰到不支持的节点类型，抛出异常', () => {
    const value = Value.create({
      document: Document.create({
        nodes: [Block.create({ type: 'unstyle' })],
      }),
    });
    expect(() => Mo.valueToJsonML(value)).toThrow();
  });
});
